@import "https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap";
body {
  --blue-color: #00acdc;
  background-color: #a19f9f;
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  cursor: pointer;
  color: #000;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.link__logo {
  cursor: pointer;
  text-decoration: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.logo {
  object-fit: contain;
}

.is-active, .nav__link:hover {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
